import React from 'react';

import './dropdown-menu.css';


const _DropdownMenuItem = ({ text, link }) => {
  return (
    <li>
      { link && (
        <a href={ link }>
          <p className='menu-lable'>{ text }</p>
        </a>
      ) }
      { !link && (
        <p className='menu-lable'>{ text }</p>
      ) }
    </li>
  );
};

export const DropdownMenuItem = _DropdownMenuItem;

const _DropdownMenu = ({ title, link, children }) => {
  
  return (
    <div class="dropdown">
      <div className='dropdown-button-container'>
        <div className='dropdown-button'>
          { link && (
            <a href={ link }>
              <span className='dropdown-button-label'>
              { title }
            </span>
            </a>
          ) }
          { !link && (
            <span className='dropdown-button-label'>
              { title }
            </span>
          ) }
        </div>
      </div>
      { children && (
        <ul className="dropdown-content">
          { children }
        </ul>
      ) }
      
    </div>
  );
};

export const DropdownMenu = _DropdownMenu;
